<script>
  export default {
    data: () => ({
      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: 'Value',
          align: 'start',
          value: 'name',
        },
        { text: '', value: 'actions', sortable: false },
      ],
      region: [
