        },
      ],
      partSection: [
        { name: 'ALL' },
        { name: 'Out Front' },
        { name: 'Out Side' },
        { name: 'Out Rear' },
        { name: 'Inner' },
      ],
      patternPart: [
        { name: 'Bumper' },
        { name: 'Front Fender' },
        { name: 'Head Light' },
        { name: 'Hood' },
      ],
      model: [],
      subPart: [],
      editedIndex: -1,
      editedItem: {
        name: ''
      },
      defaultItem: {
        name: ''
      },
    }),
    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New' : 'Edit'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    methods: {

      editItem (item) {
        this.editedIndex = this.region.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.region.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.region.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.region[this.editedIndex], this.editedItem)
        } else {
          this.region.push(this.editedItem)
        }
        this.close()
      },
    },
  }
</script>
